import React from 'react';
import { Button, Card, ButtonToolbar } from 'react-bootstrap'

import img1 from './images/turbodirect.png'
import img2 from './images/newellspace.png'
import img3 from './images/mars.jpg'
import img5 from './images/rattle.png'
import img6 from './images/jacman.JPG'
import img7 from './images/bloodbrother.JPG'



export default [{
  name: 'Turbo Direct',
  render: (
<Card className="text-center" style={{width: '25rem'}}>
<Card.Header>Turbo Direct</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img1} style={{ height: '100px', width: '250px' }}/>
  <Card.Body>
    <Card.Title>Turbo Direct</Card.Title>
    <Card.Text>
Turbo Direct is a small E-Commerce store for hard to find turbochargers. This webpage was built with React and Bootstrap 4 for a clean and modern feeling. Each item can be added to a cart with the option to add a shipping charge.
    </Card.Text>
     <Button variant="success" size="lg" block href="https://turbodirect.netlify.com" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/TurboDirect" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: React, Redux, MaterialUI</Card.Footer>
</Card>
  )
}, {
  name: 'Newell Automotive Service',
  render: (
    <Card className="text-center" style={{width: '25rem'}}>
<Card.Header>Newell Auto Service</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img2} style={{ height: '200px', width: '75%' }}/>
  <Card.Body>
    <Card.Title>Newell Auto Service</Card.Title>
    <Card.Text>
Built for a local mobile automotive-repair service, this web page utilizes Gatsby.js as an extension of React for lightning fast speed between pages. This web page was increased customer-acquisition significantly.
    </Card.Text>
     <Button variant="success" size="lg" block href="https://newellautoservice.netlify.com" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/NewellAutoService" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: React, Gatsby</Card.Footer>
</Card>
       )
}, {
  name: 'BloodBrother',
  render: (
<Card className="text-center" style={{width: '25rem'}}>
<Card.Header>Blood Brother</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img7} style={{ height: 'auto', width: '75%', paddingTop: '2em' }}/>
  <Card.Body>
    <Card.Title>Blood Brother</Card.Title>
    <Card.Text>
    This is a food and recommendation app for Diabetics. It takes a users blood glucose input and outputs relevant foods and drinks with carb counts acceptable for the current glucose level. This information is pulled from a USDA API, sorts the JSON data based on its carb values and returns information in an easy to read list format. On this project I learned a lot on making API calls and how to filter through the levels of indexes to the desired information.    </Card.Text>
     <Button variant="success" size="lg" block href="https://bloodbrother.netlify.com/" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/BloodBrother" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: Vanilla JavaScript</Card.Footer>
</Card>
  )
}, {
  name: 'RattleScan',
  render: (
<Card className="text-center" style={{width: '25rem'}}>
<Card.Header>RattleScan</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img5} style={{ height: '200px', width: '250px' }}/>
  <Card.Body>
    <Card.Title>RattleScan</Card.Title>
    <Card.Text>
    This web app is for finding and reporting rattlesnake sightings. Other users can use this information to avoid potentially dangerous areas. Skills: JavaScript, HTML, CSS, Google Maps API.    </Card.Text>
     <Button variant="success" size="lg" block href="https://rattlescan.netlify.com" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/RattleScan" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: Vanilla JavaScript</Card.Footer>
</Card>
  )
},{
  name: 'JacMan',
  render: (
<Card className="text-center" style={{width: '25rem'}}>
<Card.Header>JacMan</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img6} style={{ height: 'auto', width: '75%', paddingTop: '2em' }}/>
  <Card.Body>
    <Card.Title>JacMan</Card.Title>
    <Card.Text>
Using Phaser3, I recreated a simple platformer loosly based on Pacman for my son Jack. This was a great learning experience for me because I experienced JavaScript having full control over a canvas.
    </Card.Text>
     <Button variant="success" size="lg" block href="https://jacman.netlify.com" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/JacMan" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: JavaScript, Phaser3</Card.Footer>
</Card>
  )
}, {
  name: 'Mars Weather',
  render: (
<Card className="text-center" style={{width: '25rem'}}>
<Card.Header>Mars Weather App</Card.Header>
  <Card.Img className="mx-auto" variant="top" src={img3}  style={{ height: '200px', width: '75%' }}/>
  <Card.Body>
    <Card.Title>Mars Weather App</Card.Title>
    <Card.Text>
    Ever wonder how cold Mars is right now? This web app brings that very information to you and more. This captures data from the InSight Rover on Mars and parses it into a readable UI. Built from scratch with React.JS.    </Card.Text>
     <Button variant="success" size="lg" block href="https://marsweather.netlify.com" target="_blank">View Project</Button>
     <Button variant="dark" size="lg" block href="https://github.com/NateSpring/MarsWeatherForecast" target="_blank">Github Repo</Button>
  </Card.Body>
  <Card.Footer>Created With: React</Card.Footer>
</Card>
  )
}
]


