import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import img6 from './roundProfileMe.png'
import './style.scss';

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
        <h1 style={{ color: colorPrimary }}>About</h1>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              
            I'm Nathan Spring, I love to build and learn! My background of customer-service and problem-solving has led me down a unique path in software development. I am fueled by my passion to help others through building web-apps to solve specific problems. My hunger for knowledge and determination to succeed has contributed to months of consistent learning and tackling intricate projects. The current technology stacks I'm using are: HTML/CSS, JavaScript, Node.JS, React, jQuery, and Bootstrap; although I'm constantly learning new frameworks, libraries, and technologies. In my spare time I love to fix anything that comes my way, especially when it challenges how far I can think outside of the box. If you'd like to hear more about my journey as a self-taught web developer, please get in contact with me!
            </p>
                                 <img src={img6} alt="itsMe!" />

          </div>

        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio" />
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



